body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-size: 1.1rem; }

.App {
  text-align: left;
  position: relative; }
  .App .Checked {
    background-color: #26f17b; }
  .App .Checking {
    background-color: #e69318; }
  .App .InProgress {
    background-color: #e3f126; }
  .App .ToDo {
    background-color: #bbbaba; }
  .App .failed {
    background-color: #f12626; }
  .App .table {
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0); }
  .App .studentName {
    background-color: #59b3fc; }
  .App .css-10nd86i {
    position: absolute;
    top: 30px;
    left: 20%;
    width: 60%; }
  .App td {
    min-height: 25px; }
  .App .link {
    padding-left: 4px;
    padding-right: 4px;
    text-decoration: none;
    color: black; }
  .App .mentor-table {
    position: absolute;
    top: 80px;
    text-align: center; }
  .App .task-table {
    position: absolute;
    top: 140px; }
    .App .task-table .taskName {
      min-width: 250px; }
  .App .description-table {
    position: absolute;
    top: 520px; }
    .App .description-table .description-table__cell {
      min-width: 180px;
      padding-left: 10px; }
  .App .cell {
    min-width: 100px;
    text-align: center; }
  .App .mentorTitle,
  .App .mentorName {
    min-width: 300px;
    font-weight: bold;
    font-size: 1.3em; }
  .App .taskName {
    text-align: left; }

